                            <p className="text-lg md:text-xl text-zinc-400 leading-relaxed group-hover:text-zinc-300 transition-colors">
                                Product systems exist to create an unfair advantage. By reducing coordination friction and intelligence synthesis time, we enable higher operational tempo.
                            </p>
