function SectionWrapper({ children, className = "" }: { children: React.ReactNode, className?: string }) {
    const ref = useRef(null)
    const isInView = useInView(ref, { once: false, margin: "-20%" })

    return (
        <motion.section
            ref={ref}
            initial={{ opacity: 0, y: 32 }}
            animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 32 }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            className={`min-h-screen py-32 px-6 flex flex-col justify-center ${className}`}
        >
            <div className="max-w-5xl mx-auto w-full">
                {children}
            </div>
        </motion.section>
    )
}

function QuizCard({ question, options, correctAnswer, onSolve, isSolved, label }: {
    question: string,
    options: string[],
    correctAnswer: number,
    onSolve: () => void,
    isSolved: boolean,
    label: string
}) {
    const [selected, setSelected] = useState<number | null>(null)
    const [error, setError] = useState(false)

    const handleSelect = (idx: number) => {
        if (isSolved) return
        setSelected(idx)
        if (idx === correctAnswer) {
            onSolve()
            setError(false)
        } else {
            setError(true)
            setTimeout(() => setError(false), 500)
        }
    }

    return (
        <div className={`mt-32 p-12 border transition-all duration-500 ${isSolved ? 'border-zinc-500/50 bg-zinc-500/5' : 'border-zinc-800 bg-white/[0.02]'}`}>
            <div className="space-y-8 max-w-2xl mx-auto">
                <div className="flex items-center gap-4">
                    <span className="text-[10px] font-mono font-bold tracking-[0.4em] text-zinc-500 uppercase">{label} // Validation Required</span>
                    <div className="h-px flex-1 bg-zinc-800" />
                </div>

                <h4 className="text-xl md:text-2xl font-light text-zinc-200">{question}</h4>

                <div className="grid gap-3">
                    {options.map((opt, i) => (
                        <button
                            key={i}
                            onClick={() => handleSelect(i)}
                            disabled={isSolved}
                            className={`p-4 text-left text-xs uppercase tracking-widest border transition-all flex items-center justify-between group
                                ${isSolved && i === correctAnswer ? 'border-zinc-400 text-white bg-zinc-800' :
                                    isSolved ? 'border-zinc-800 text-zinc-600' :
                                        selected === i && i !== correctAnswer ? 'border-red-900 bg-red-900/10 text-red-500' :
                                            'border-zinc-800 text-zinc-400 hover:border-zinc-500 hover:text-white hover:bg-white/5'}
                            `}
                        >
                            <span>{opt}</span>
                            {isSolved && i === correctAnswer && (
                                <span className="text-[10px] lowercase text-zinc-500 italic">verified</span>
                            )}
                        </button>
                    ))}
                </div>

                {error && (
                    <motion.p
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        className="text-[10px] font-mono text-red-500 uppercase tracking-widest text-center"
                    >
                        Calibration Error. Please re-evaluate approach.
                    </motion.p>
                )}
            </div>
        </div>
    )
}

